'use strict';
import { LOAN_APPLICATION, LOAN_TESTONETEST} from '@/config/router.js';
import simpleAddress from "@/components/simple-address-normal/simple-address.nvue"
export default {
	components: {
	            simpleAddress
				
	        },
	
	data() {
		return{
			cityPickerValueDefault: [0, 0, 1],
			                pickerText: '',
							apptArray: ['中国', '美国', '巴西', '日本'],
							apptIndex: 0,
		}
	},
	methods:{
		openAddres() {
			
		                this.$refs.simpleAddress.open();
		            },
		            onConfirm(e) {
						
						var b=e.label.split('-')
						// console.log(e)
						
		                this.pickerText = b[0]+' '+ b[1]+' '+b[2]
		            },
		open(){
		         this.$refs.popup.open()
		      },
		cancel() {
			this.$refs.popup.close()
		},
		change(e) {
			console.log('是否打开:' + e.show)
		},
		apptBindPickerChange: function(e) {
		            console.log('picker发送选择改变，携带值为', e.target.value)
		            this.apptIndex = e.target.value
		        },
	},
	async onLoad() {}
};
